import React from 'react';
import { MapPin } from 'lucide-react';

const LocationMap = ({ latitude, longitude, location }) => {
  // URL de OpenStreetMap con marcador
  const mapUrl = `https://www.openstreetmap.org/export/embed.html?bbox=${longitude - 0.5},${latitude - 0.5},${longitude + 0.5},${latitude + 0.5}&layer=mapnik&marker=${latitude},${longitude}`;
  
  return (
    <div className="card">
      <h3 className="card-title flex items-center mb-4">
        <MapPin className="w-5 h-5 mr-2 text-green-600" />
        Ubicación del Sistema
      </h3>

      {/* Mapa */}
      <div className="mb-4 rounded-lg overflow-hidden border-2 border-gray-200">
        <iframe
          width="100%"
          height="300"
          frameBorder="0"
          scrolling="no"
          marginHeight="0"
          marginWidth="0"
          src={mapUrl}
          title="Mapa de ubicación"
        />
      </div>

      {/* Información de coordenadas */}
      <div className="grid grid-cols-2 gap-4">
        <div className="p-3 bg-blue-50 rounded-lg">
          <p className="text-xs text-gray-600 mb-1">Latitud</p>
          <p className="text-lg font-bold text-blue-600">{latitude}°</p>
        </div>
        <div className="p-3 bg-green-50 rounded-lg">
          <p className="text-xs text-gray-600 mb-1">Longitud</p>
          <p className="text-lg font-bold text-green-600">{longitude}°</p>
        </div>
      </div>

      {location && (
        <div className="mt-4 p-3 bg-gray-50 rounded-lg">
          <p className="text-sm text-gray-600">
            📍 <strong>{location}</strong>
          </p>
        </div>
      )}

      {/* Link a Google Maps */}
      <div className="mt-4">
        <a
          href={`https://www.google.com/maps?q=${latitude},${longitude}`}
          target="_blank"
          rel="noopener noreferrer"
          className="text-sm text-blue-600 hover:text-blue-800 hover:underline"
        >
          🗺️ Ver en Google Maps →
        </a>
      </div>
    </div>
  );
};

export default LocationMap;
